'use client';

import { useState, useCallback } from 'react';
import Header from '@/components/Header';
import ProjectOverview from '@/components/ProjectOverview';
import ImportData from '@/components/ImportData';
import ComplianceStatus from '@/components/ComplianceStatus';
import QuickActions from '@/components/QuickActions';
import BP220Standards from '@/components/BP220Standards';
import SubdivisionMap from '@/components/SubdivisionMap';
import { LotData, ProjectStats, ComplianceResult } from '@/types/compliance';
import { parseCSVData, validateAllLots } from '@/utils/complianceValidator';

export default function Home() {
  const [lots, setLots] = useState<LotData[]>([]);
  const [complianceResults, setComplianceResults] = useState<ComplianceResult[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [stats, setStats] = useState<ProjectStats>({
    totalLots: 0,
    totalArea: 12.5,
    compliantLots: 0,
    violations: 0,
    suggestions: 0,
  });

  const updateStats = useCallback((lotData: LotData[], results: ComplianceResult[]) => {
    const totalLots = lotData.length;
    const compliantLots = results.filter(r => r.compliant).length;
    const violations = results.reduce((acc, r) => acc + r.violations.length, 0);
    const suggestions = results.reduce((acc, r) => acc + r.suggestions.length, 0);
    const totalArea = lotData.reduce((acc, lot) => acc + lot.area, 0) / 10000; // Convert to hectares

    setStats({
      totalLots,
      totalArea: Math.round(totalArea * 10) / 10,
      compliantLots,
      violations,
      suggestions,
    });
  }, []);

  const handleFileUpload = useCallback(async (file: File) => {
    setIsLoading(true);
    try {
      const content = await file.text();
      const parsedLots = parseCSVData(content);
      const results = validateAllLots(parsedLots);
      
      // Update lots with compliance results
      const updatedLots = parsedLots.map((lot, index) => ({
        ...lot,
        compliant: results[index].compliant,
        violations: results[index].violations,
        suggestions: results[index].suggestions,
      }));

      setLots(updatedLots);
      setComplianceResults(results);
      updateStats(updatedLots, results);
    } catch (error) {
      console.error('Error processing file:', error);
      alert('Error processing CSV file. Please check the format and try again.');
    } finally {
      setIsLoading(false);
    }
  }, [updateStats]);

  const handleRunValidation = useCallback(() => {
    if (lots.length === 0) return;
    
    setIsLoading(true);
    setTimeout(() => {
      const results = validateAllLots(lots);
      const updatedLots = lots.map((lot, index) => ({
        ...lot,
        compliant: results[index].compliant,
        violations: results[index].violations,
        suggestions: results[index].suggestions,
      }));

      setLots(updatedLots);
      setComplianceResults(results);
      updateStats(updatedLots, results);
      setIsLoading(false);
    }, 1500);
  }, [lots, updateStats]);

  const handleAutoCorrect = useCallback(() => {
    alert('Auto-correct functionality would be implemented here');
  }, []);

  const handleGenerateReport = useCallback(() => {
    const reportData = {
      projectStats: stats,
      lots: lots,
      complianceResults: complianceResults,
      timestamp: new Date().toISOString(),
    };
    
    const blob = new Blob([JSON.stringify(reportData, null, 2)], {
      type: 'application/json',
    });
    
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'bp220-compliance-report.json';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }, [stats, lots, complianceResults]);

  const handleExportProject = useCallback(() => {
    const projectData = {
      lots: lots,
      stats: stats,
      complianceResults: complianceResults,
      exportDate: new Date().toISOString(),
    };
    
    const blob = new Blob([JSON.stringify(projectData, null, 2)], {
      type: 'application/json',
    });
    
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'bp220-project-export.json';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }, [lots, stats, complianceResults]);

  const handleSaveChanges = useCallback(() => {
    // In a real application, this would save to a backend
    localStorage.setItem('bp220-project', JSON.stringify({
      lots,
      stats,
      complianceResults,
      lastSaved: new Date().toISOString(),
    }));
    alert('Changes saved successfully!');
  }, [lots, stats, complianceResults]);

  return (
    <div className="min-h-screen bg-gray-50">
      <Header 
        onExportProject={handleExportProject}
        onSaveChanges={handleSaveChanges}
      />
      
      <div className="flex">
        {/* Left Sidebar */}
        <div className="w-80 bg-gray-50 p-6 space-y-6 border-r border-gray-200">
          <ProjectOverview stats={stats} />
          <ImportData onFileUpload={handleFileUpload} isLoading={isLoading} />
          <ComplianceStatus stats={stats} />
          <QuickActions
            onRunValidation={handleRunValidation}
            onAutoCorrect={handleAutoCorrect}
            onGenerateReport={handleGenerateReport}
            isLoading={isLoading}
            hasData={lots.length > 0}
          />
          <BP220Standards />
        </div>
        
        {/* Main Content */}
        <div className="flex-1 p-6">
          <SubdivisionMap
            lots={lots}
            hasData={lots.length > 0}
            onFileUpload={handleFileUpload}
          />
        </div>
      </div>
    </div>
  );
}
