import { LotData, ComplianceResult, BP220Standards } from '@/types/compliance';

export const BP220_STANDARDS: BP220Standards = {
  minimumLotAreas: {
    rowhouse: 64,
    duplex: 72,
    'single-detached': 96,
  },
  minimumFrontage: {
    rowhouse: 3,
    duplex: 4,
    'single-detached': 6,
  },
  roadWidths: {
    minor: 6.5,
    major: 8.0,
    main: '10-15',
  },
};

export function validateLotCompliance(lot: LotData): ComplianceResult {
  const violations: string[] = [];
  const suggestions: string[] = [];

  // Check minimum lot area
  const minArea = BP220_STANDARDS.minimumLotAreas[lot.type];
  if (lot.area < minArea) {
    violations.push(`Lot area ${lot.area}sqm is below minimum ${minArea}sqm for ${lot.type}`);
  }

  // Check minimum frontage
  const minFrontage = BP220_STANDARDS.minimumFrontage[lot.type];
  if (lot.frontage < minFrontage) {
    violations.push(`Frontage ${lot.frontage}m is below minimum ${minFrontage}m for ${lot.type}`);
  }

  // Add suggestions for optimization
  if (lot.area < minArea * 1.1) {
    suggestions.push(`Consider increasing lot area for better compliance margin`);
  }

  if (lot.frontage < minFrontage * 1.1) {
    suggestions.push(`Consider increasing frontage for better accessibility`);
  }

  const compliant = violations.length === 0;

  return {
    lotId: lot.id,
    compliant,
    violations,
    suggestions,
  };
}

export function parseCSVData(csvContent: string): LotData[] {
  const lines = csvContent.trim().split('\n');
  const headers = lines[0].split(',').map(h => h.trim());
  
  return lines.slice(1).map((line, index) => {
    const values = line.split(',').map(v => v.trim());
    
    return {
      id: values[0] || `lot-${index + 1}`,
      coordinates: [], // Would be parsed from coordinate data
      area: parseFloat(values[1]) || 0,
      frontage: parseFloat(values[2]) || 0,
      type: (values[3] as 'rowhouse' | 'duplex' | 'single-detached') || 'single-detached',
      roadAccess: (values[4] as 'minor' | 'major' | 'main') || 'minor',
      compliant: false,
      violations: [],
      suggestions: [],
    };
  });
}

export function validateAllLots(lots: LotData[]): ComplianceResult[] {
  return lots.map(lot => validateLotCompliance(lot));
}
