'use client';

import { LotData } from '@/types/compliance';

interface SubdivisionMapProps {
  lots: LotData[];
  hasData: boolean;
  onFileUpload: (file: File) => void;
}

export default function SubdivisionMap({ lots, hasData, onFileUpload }: SubdivisionMapProps) {
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file && file.type === 'text/csv') {
      onFileUpload(file);
    }
  };

  if (!hasData) {
    return (
      <div className="bg-white rounded-lg border border-gray-200 p-8">
        <div className="text-center">
          <div className="w-24 h-24 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <svg className="w-12 h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
            </svg>
          </div>
          
          <h3 className="text-xl font-semibold text-gray-900 mb-2">No Subdivision Data</h3>
          <p className="text-gray-600 mb-6">
            Upload a CSV file with lot coordinates to begin compliance validation.
          </p>
          
          <label className="inline-flex items-center px-6 py-3 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 cursor-pointer transition-colors">
            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
            </svg>
            Upload CSV File
            <input
              type="file"
              accept=".csv"
              onChange={handleFileSelect}
              className="hidden"
            />
          </label>
        </div>
      </div>
    );
  }

  const compliantLots = lots.filter(lot => lot.compliant).length;
  const totalLots = lots.length;

  return (
    <div className="bg-white rounded-lg border border-gray-200">
      <div className="border-b border-gray-200 p-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <h3 className="text-lg font-semibold text-gray-900">Subdivision Layout Map</h3>
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-600">View:</span>
              <select className="text-sm border border-gray-300 rounded px-2 py-1">
                <option>Compliance Status</option>
                <option>Lot Types</option>
                <option>Area Analysis</option>
              </select>
            </div>
          </div>
          
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-green-500 rounded-full"></div>
              <span className="text-sm text-gray-600">Compliant</span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-red-500 rounded-full"></div>
              <span className="text-sm text-gray-600">Violation</span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-orange-500 rounded-full"></div>
              <span className="text-sm text-gray-600">Suggested Fix</span>
            </div>
          </div>
        </div>
      </div>
      
      <div className="p-8">
        <div className="text-center">
          <div className="w-24 h-24 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <svg className="w-12 h-12 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
          
          <h3 className="text-xl font-semibold text-gray-900 mb-2">All Lots Compliant</h3>
          <p className="text-gray-600">
            No compliance violations found in the current subdivision layout.
          </p>
          
          <div className="mt-6 p-4 bg-gray-50 rounded-lg">
            <div className="grid grid-cols-3 gap-4 text-center">
              <div>
                <p className="text-2xl font-bold text-gray-900">{totalLots}</p>
                <p className="text-sm text-gray-600">Total Lots</p>
              </div>
              <div>
                <p className="text-2xl font-bold text-green-600">{compliantLots}</p>
                <p className="text-sm text-gray-600">Compliant</p>
              </div>
              <div>
                <p className="text-2xl font-bold text-red-600">{totalLots - compliantLots}</p>
                <p className="text-sm text-gray-600">Violations</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
